$breakpoints-up: (
    'medium': '40em',
    'large': '64em',
    'xlarge': '87.5em',
);

$breakpoints-down: (
    'small': '39.9375em',
    'medium': '63.9375em',
    'large': '87.4375em',
);

@mixin breakpoint($size) {
    @media (min-width: map-get($breakpoints-up, $size)) {
        @content;
    }
}

@mixin breakpointdown($size) {
    @media (maxwidth: map-get($breakpoints-down, $size)) {
        @content;
    }
}
